/* ## Forms
--------------------------------------------- */

input,
select,
textarea {
	width: 100%;
	padding: 15px;
	border: $border-width $border-style $light-grey;
	color: $forms--font-color;
	background-color: $forms__background-color;
	font-size: $forms--font-size;
	font-weight: $font-weight--regular;
}

input:focus,
textarea:focus {
	border: $border-width $border-style $medium-grey;
	outline: none;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
	width: auto;
}

::-moz-placeholder {
	opacity: 1;
	color: $dark-grey;
}

::-webkit-input-placeholder {
	color: $dark-grey;
}

button {
	width: auto;
	padding: 15px 30px;
	border: 0;
	color: $button--font-color;
	background-color: $button--background-color;
	font-size: $button--font-size;
	font-weight: $font-weight--semibold;
	white-space: normal;
	text-decoration: none;
	cursor: pointer;

	&:focus,
	&:hover {
		color: $button--font-color-hover;
		background-color: $button--background-color-hover;
	}

	&.small {
		padding: 8px 16px;
	}
}

